<template>
  <top-bar :title="'区域客流分析'" :left="left" :right="right" :max="max" :values="values" />
</template>
<script>
import TopBar from "@/components/charts/TopBar.vue";

export default {
  mounted() {
    this.$getData("/tourism/shopping").then(list => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: []
    };
  },
  computed: {
    left() {
      return this.list.map(({ type }) => type);
    },
    values() {
      return this.list.map(({ value }) => Number(value));
    },
    right() {
      return this.values.map(item => item + "人次");
    },
    max() {
      return Math.max(...this.values) * 1.2;
    }
  },
  components: {
    TopBar
  }
};
</script>