<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  props: {
    title: {},
    left: { default: [] },
    right: { default: [] },
    values: { default: [] },
    max: {},
    showIndex: {
      default: false,
    },
    monthSelect: { default: true },
  },
  mounted() {
    this.$unlock({ monthSelect: this.monthSelect });
  },
  computed: {
    option() {
      return {
        title: {
          text: this.title,
          x: "center",
          top: "8%",
          textStyle: {
            fontSize: "100%",
            fontWeight: "normal",
            color: "#03D3D3",
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        grid: {
          left: "12%",
          right: "12%",
          top: "30%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          max: this.max,
          axisLabel: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          splitLine: {
            show: false,
          },
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            axisLabel: {
              color: "#fff",
              fontSize: "80%",
              padding: [0, 10, 0, 10],
              formatter: function (params) {
                // 超出省略
                params = params.toString();
                var maxlength = 8;
                if (params.length > maxlength) {
                  return params.substring(0, maxlength - 1) + "...";
                } else {
                  return params;
                }
              },
            },
            data: this.left.map((item, index) =>
              this.showIndex ? `${index + 1}. ${item}` : item
            ),
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
          },
          {
            type: "category",
            inverse: true,
            axisLabel: {
              color: "#fff",
              fontSize: "80%",
              padding: [0, 10, 0, 10],
            },
            data: this.right,
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            yAxisIndex: 0,
            barWidth: "36%",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(0,0,0,0.41)",
            },
            itemStyle: {
              showBackground: true,
              normal: {
                color: function (params) {
                  return [
                    {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#D3F680",
                        },
                        {
                          offset: 1,
                          color: "#ABFFF4",
                        },
                      ],
                    },
                    {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#809EF6",
                        },
                        {
                          offset: 0.5,
                          color: "#B3C6FF",
                        },
                        {
                          offset: 1,
                          color: "#ABFFF4",
                        },
                      ],
                    },
                    {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#5CB1EC",
                        },
                        {
                          offset: 1,
                          color: "#5B80EB",
                        },
                      ],
                    },
                    {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#F6AF80",
                        },
                        {
                          offset: 1,
                          color: "#F7D48F",
                        },
                      ],
                    },
                    {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#51F1ED",
                        },
                        {
                          offset: 0.5,
                          color: "#51F1ED",
                        },
                        {
                          offset: 1,
                          color: "#ABFFF4",
                        },
                      ],
                    },
                  ][params.dataIndex];
                },
              },
            },
            type: "bar",
            data: this.values,
          },
        ],
      };
    },
  },
  components: {
    Echart,
  },
};
</script>